import { AfterViewInit, Component, OnInit, Injectable, Inject, ElementRef, Renderer, PLATFORM_ID } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';
import {Globals} from '../../globals';
import { Title, Meta, DOCUMENT } from '@angular/platform-browser';
import { MetaService } from '@ngx-meta/core';
import 'jquery-countdown';

import { AuthenticationService } from '../../services/authentication.service';
import { UrlsService } from '../../services/urls.service';

declare var $: any;

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

    loginForm: FormGroup;
    loading = false;
    submitted = false;
    returnUrl: string;
    error = '';
    url: any = null;
    title: any = '';
    show: any = false;
    id_url: any = null;

    constructor(
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private router: Router,
        private authenticationService: AuthenticationService,
        private globals: Globals,
        private urlsService: UrlsService, 
        private readonly meta: Meta, 
        private titleService: Title, 
        @Inject(DOCUMENT) private document) {}

    ngOnInit() {
        this.id_url = this.route.snapshot.paramMap.get('id');
        this.authenticationService.logout();
        this.globals.isLogued = false;

        this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';

        this.loginForm = this.formBuilder.group({
            correo: ['', [Validators.required, Validators.email]],
            password: ['', Validators.required]
        });
        $('body').addClass("off-canvas-sidebar");
    }

    ngAfterViewInit(): void {
        
    }

    get f() { return this.loginForm.controls; }

    onSubmit() {
        this.submitted = true;
        // stop here if form is invalid
        if (this.loginForm.invalid) {
            return;
        }

        this.loading = true;
        this.authenticationService.login(this.f.correo.value, this.f.password.value)
            .pipe(first())
            .subscribe(
                data => {
                    this.globals.isLogued = true;
                    this.router.navigate([data.url]);
                },
                error => {
                    this.error = error;
                    this.loading = false;
                    this.submitted = false;
                    this.loginForm.get('correo').setValue('');
                    this.loginForm.get('password').setValue('');
                    this.showMessage(error.logins);
                });
    }

    showMessage(message: string) {
        $.notify({
            icon: 'notifications',
            title: ' Notificación',
            message: message,
            url: ''
        },{
            /*element: 'body',*/
            type: 'info',
            allow_dismiss: true,
            placement: {
                from: 'top',
                align: 'center'
            },
            timer: 1000,
            /*offset: {
                x: 20,
                y: 20
            },
            spacing: 10,
            z_index: 1031,
            delay: 2500,
            
            url_target: '_blank',
            mouse_over: false,*/
            animate: {
                enter: 'fadeInDown',
                exit: 'fadeOutUp'
            },
            template: '<div data-notify="container" class="col-xl-4 col-lg-4 col-11 col-sm-4 col-md-4 alert alert-{0} alert-with-icon" role="alert">' +
                      '<button mat-button  type="button" aria-hidden="true" class="close mat-button" data-notify="dismiss">  <i class="material-icons">close</i></button>' +
                      '<i class="material-icons" data-notify="icon">notifications</i> ' +
                      '<span data-notify="title">{1}</span> ' +
                      '<span data-notify="message">{2}</span>' +
                      '<div class="progress" data-notify="progressbar">' +
                        '<div class="progress-bar progress-bar-{0}" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100" style="width: 0%;"></div>' +
                      '</div>' +
                      '<a href="{3}" target="{4}" data-notify="url"></a>' +
                      '</div>'
        });
    }

}