import { AfterViewInit, Component, OnInit } from '@angular/core';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import {Globals} from '../../globals';

import { BlogService } from '../../services/blog.service';

declare var $: any;

@Component({
	selector: 'app-contact',
	templateUrl: './contact.component.html',
	styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

	contactForm: FormGroup;

 	constructor(private formBuilder: FormBuilder, private globals: Globals, private blogService: BlogService) { }

    ngOnInit() {
        $("body").addClass("off-canvas-sidebar");
        this.initForm();
    }

    ngAfterViewInit(): void {
		$('.wysiwyg-editor').trumbowyg({
			svgPath: 'assets/plantilla/vendors/bower_components/trumbowyg/dist/ui/icons.svg'
		});
    }

    initForm() {
		this.contactForm = this.formBuilder.group({
			mensaje: ['',],
			nombre: ['', [Validators.required]],
			correo: ['', [Validators.required, Validators.email]],
			asunto: ['', [Validators.required]]
		});
  	}

    get f() { return this.contactForm.controls; }

    Enviar() {
        if (this.contactForm.invalid) {
            return;
        }
		this.contactForm.get('mensaje').setValue($('#mensaje').html());
		this.blogService.crearMensaje(this.contactForm.value)
			.subscribe(data => {
				this.clearAll();
				this.showMessage("Opinión recibida");
            },
			error => {
				this.showMessage(error.errors.email[0]);
		});
    }

    clearAll() {
        $('.btn-addp').html('Enviar');
        $('#mensaje').html('');
        $('#nombre').val('');
        $('#asunto').val('');
        $('#correo').val('');
    }

    showMessage(message: string) {
        $.notify({
            icon: 'notifications',
            title: ' Notificación',
            message: message,
            url: ''
        },{
            /*element: 'body',*/
            type: 'info',
            allow_dismiss: true,
            placement: {
                from: 'top',
                align: 'center'
            },
            timer: 1000,
            animate: {
                enter: 'fadeInDown',
                exit: 'fadeOutUp'
            },
            template: '<div data-notify="container" class="col-xl-4 col-lg-4 col-11 col-sm-4 col-md-4 alert alert-{0} alert-with-icon" role="alert">' +
                      '<button mat-button  type="button" aria-hidden="true" class="close mat-button" data-notify="dismiss">  <i class="material-icons">close</i></button>' +
                      '<i class="material-icons" data-notify="icon">notifications</i> ' +
                      '<span data-notify="title">{1}</span> ' +
                      '<span data-notify="message">{2}</span>' +
                      '<div class="progress" data-notify="progressbar">' +
                        '<div class="progress-bar progress-bar-{0}" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100" style="width: 0%;"></div>' +
                      '</div>' +
                      '<a href="{3}" target="{4}" data-notify="url"></a>' +
                      '</div>'
        });
    }
}
