import { Component, Output, EventEmitter, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { Location, LocationStrategy, PathLocationStrategy, PopStateEvent } from '@angular/common';
import {trigger, animate, transition, style, query} from '@angular/animations';
import 'rxjs/add/operator/filter';
import { NavbarComponent } from './components/navbar/navbar.component';
import { Router, ActivatedRoute, ParamMap, NavigationCancel, Event, NavigationEnd, NavigationError, NavigationStart } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { CookieService } from 'ngx-cookie-service';
import {SlimLoadingBarService} from 'ng2-slim-loading-bar';
/*import PerfectScrollbar from 'perfect-scrollbar';*/
import {Globals} from './globals';
import { Observable } from 'rxjs';
import { Title, Meta } from '@angular/platform-browser';

declare var $: any;

@Component({
	  selector: 'app-root',
	  templateUrl: './app.component.html',
	  styleUrls: ['./app.component.css'],
    animations: [
        trigger('fadeAnimation', [
            transition('* => *', [
                query(
                  ':enter',
                  [style({ opacity: 0 })],
                  { optional: true }
                ),
                query(
                    ':enter',
                    [style({ opacity: 0 }), animate('0.3s ease-in', style({ opacity: 1 }))],
                    { optional: true }
                )
            ])
        ])
    ]
})
export class AppComponent {
    private _router: Subscription;
  	private lastPoppedUrl: string;
  	private yScrollStack: number[] = [];
    url: any = null;
    title: any = '';
    show: any = false;
    nombre: string = 'Seguridad';
    ubicacion: string = 'Santiago de Cuba - Cuba, Santiago';
    home: any = false;
    loadings: any = false;
 
    cookieValue = 'UNKNOWN';
    visible: boolean = true;
 
    @Output() close: EventEmitter<any> = new EventEmitter();

  	constructor(public location: Location,
                private router: Router,
                public globals: Globals,
                private route: ActivatedRoute,
                private cookieService: CookieService,
                private _loadingBar: SlimLoadingBarService) {

        this.router.events.subscribe((event: Event) => {
            this.navigationInterceptor(event);
        });
    }

  	ngOnInit() {
        this.router.events.subscribe((res) => { 
            if(this.router.url ==  "/home")
                this.home = true;
            else
                this.home = false;
        })

        this.cookieService.set( 'Cookie', 'GDPR' );
        this.cookieValue = this.cookieService.get('Cookie');
        this.globals.refer = document.referrer;
  	}
    ngAfterViewInit() {
        this.runOnRouteChange();
    }
  	isMaps(path){
      	var titlee = this.location.prepareExternalUrl(this.location.path());
      	titlee = titlee.slice( 1 );
      	if(path == titlee){
          	return false;
      	}
      	else {
          	return true;
      	}
  	}
  	runOnRouteChange(): void {

  	}
  	isMac(): boolean {
      	let bool = false;
      	if (navigator.platform.toUpperCase().indexOf('MAC') >= 0 || navigator.platform.toUpperCase().indexOf('IPAD') >= 0) {
          	bool = true;
      	}
      	return bool;
  	}

    onGRDP() {
        this.visible = !this.visible;
        if (this.visible) {        
            this.close.emit(null);
        }
    }

    private navigationInterceptor(event: Event): void {
        if (event instanceof NavigationStart) {
            this._loadingBar.start();
        }
        if (event instanceof NavigationEnd) {
            this._loadingBar.complete();
        }
        if (event instanceof NavigationCancel) {
            this._loadingBar.stop();
        }
        if (event instanceof NavigationError) {
            this._loadingBar.stop();
        }
    }
}
