import { AfterViewInit, Component, OnInit, Injectable, Inject, ElementRef, Renderer, PLATFORM_ID, AfterViewChecked } from '@angular/core';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { Location } from '@angular/common';
import { Title, Meta, DOCUMENT } from '@angular/platform-browser';
import { MetaService } from '@ngx-meta/core';
import {Globals} from '../../../globals';
import * as moment from '../../../../assets/plantilla/vendors/bower_components/moment/moment.js';

import { BlogService } from '../../../services/blog.service';

import * as $ from 'jquery';

@Component({
	selector: 'app-noticia',
	templateUrl: './noticia.component.html',
	styleUrls: ['./noticia.component.css']
})
export class NoticiaComponent implements OnInit, AfterViewChecked {

	id_url: any = null;
	categoria: any = '';
	login: any = '';
	titulo: any = '';
	foto: any = '';
	creada: any = '';
	video: any = null;
	popular: any = [];
	visitas: any = 0;
	fecha: any = '';
	fin: any = '';
    twitter: any = false;
    twvideo: any = '';

	constructor(private route: ActivatedRoute,
				private blogService: BlogService,
				private globals: Globals,
				private _sanitizer: DomSanitizer,
				private local: Location,
				private router: Router,
				private readonly meta: Meta,
				private titleService: Title,
				@Inject(DOCUMENT) private document) {}

	ngAfterViewInit(): void {
		this.blogService.getPopular()
            .subscribe(data => {
                let da: any = data;
                this.popular = da;
            })
    }

    ngAfterViewChecked() {
        const anchors = document.getElementsByTagName('a');
        for (let i = 0; i < anchors.length; i++) {
            let valores = anchors[i].href.split('/');
            if(Number.isInteger(parseInt(valores[valores.length - 1]))) {
                const a = 'àáâäæãåāăąçćčđďèéêëēėęěğǵḧîïíīįìłḿñńǹňôöòóœøōõőṕŕřßśšşșťțûüùúūǘůűųẃẍÿýžźż·/_,:;'
                const b = 'aaaaaaaaaacccddeeeeeeeegghiiiiiilmnnnnoooooooooprrsssssttuuuuuuuuuwxyyzzz------'
                const p = new RegExp(a.split('').join('|'), 'g')

                let fin = anchors[i].title.toString().toLowerCase()
                    .replace(/\s+/g, '-') // Replace spaces with -
                    .replace(p, c => b.charAt(a.indexOf(c))) // Replace special characters
                    .replace(/&/g, '-and-') // Replace & with 'and'
                    .replace(/[^\w\-]+/g, '') // Remove all non-word characters
                    .replace(/\-\-+/g, '-') // Replace multiple - with single -
                    .replace(/^-+/, '') // Trim - from start of text
                    .replace(/-+$/, '') // Trim - from end of text
                if(fin != '') {
                    anchors[i].setAttribute('href', '/' + $(anchors[i]).attr('categoria') + '/' + fin);
                }
            }
        }
        if(this.fin != '')
        	this.local.replaceState(this.fin);
    }

	ngOnInit() {
        /*let scripta: any = this.document.createElement('script');
        scripta.async = true;
        scripta.src = "https://jsc.mgid.com/c/r/cropersviral.com.797653.js";
        this.document.body.appendChild(scripta);
        let script: any = this.document.createElement('script');
        script.async = true;
        script.src = "https://jsc.mgid.com/c/r/cropersviral.com.797649.js";
        this.document.body.appendChild(script);
        let scriptcon: any = this.document.createElement('script');
        scriptcon.async = true;
        scriptcon.src = "https://jsc.mgid.com/c/r/cropersviral.com.802483.js";
        this.document.body.appendChild(scriptcon);*/
        $('body').addClass("off-canvas-sidebar");
		let us = JSON.parse(localStorage.getItem('currentUser'));
        setTimeout(() => {
            this.login = us != null ? true : false;
        }, 0);
		this.id_url = this.route.snapshot.paramMap.get('id');
		//setTimeout(() => {
			this.blogService.getNoti(this.id_url)
				.subscribe(data => {
					let da: any = data;

					this.titleService.setTitle(da.titulo);
                    this.meta.updateTag({name: 'title', content: da.titulo});
                    this.meta.updateTag({name: 'description', content: da.bloque_plano});
                    this.meta.updateTag({property: 'og:url', content: this.globals.globalUrl + '/' + da.categoria.categoria + '/' + da.id});
                    this.meta.updateTag({property: 'og:title', content: da.titulo});
                    this.meta.updateTag({property: 'og:description', content: da.bloque_plano});
                    this.meta.updateTag({property: 'og:image', content: this.globals.urlPhoto+da.foto});
                    this.meta.updateTag({property: 'og:image:width', content: '740'});
                    this.meta.updateTag({property: 'og:image:height', content: '370'});

                    this.meta.updateTag({name: 'twitter:card', content: "summary"});
                    this.meta.updateTag({name: 'twitter:site', content: this.globals.globalUrl + '/' + da.categoria.categoria + '/' + da.id});
                    this.meta.updateTag({name: 'twitter:title', content: da.titulo});
                    this.meta.updateTag({name: 'twitter:description', content: da.bloque_plano});
                    this.meta.updateTag({name: 'twitter:image', content: this.globals.urlPhoto+da.foto});

                    let link: any = this.document.createElement('link');
                    link.async = true;
                    link.rel = 'canonical';
                    link.href = this.globals.globalUrl + '/' + da.categoria.categoria + '/' + da.id;
                    this.document.head.appendChild(link);

					this.categoria = da.categoria.categoria;
					this.titulo = da.titulo;
					$('#bloque1').html(da.bloque1);
					$('#bloque2').html(da.bloque2);
					this.foto = this.globals.urlPhoto + da.foto;
					this.creada = da.users.nombre_apellidos;// + ', ' + moment(da.fecha).locale('es').fromNow();
					this.visitas = da.visitas;
					this.fecha = moment(da.fecha).format('YYYY/MM/DD H:mm');
					if(da.video != null) {
                        let twi = da.video.match('twitter.com');
                        if(twi != null) {
                            let arr = da.video.split('/');
                            this.twitter = true;
                            this.twvideo = arr[arr.length - 1];
                        }
                        else {
                            let results = da.video.match('[\\?&]v=([^&#]*)');
                            let vid = (results === null) ? da.video : results[1];
                            this.video = this._sanitizer.bypassSecurityTrustResourceUrl('https://www.youtube.com/embed/' + vid);
                        }
					}
		            this.local.replaceState(da.categoria.categoria + '/' + da.slug);
				},
				err => {
                        this.router.navigate(['404']);
                }
			)
		//}, 0);
	}

	getFecha(fecha) {
        return moment(fecha).format('YYYY/MM/DD H:mm');
    }
}
