import { Routes, RouterModule } from '@angular/router';

import { AuthGuards } from '../guard/auth.guard';
import { HomeGuard } from '../guard/home.guard';
import { ReferGuard } from '../guard/refer.guard';

import { HomeComponent } from '../pages/blog/home/home.component';
import { AddnoticeComponent } from '../pages/blog/addnotice/addnotice.component';
import { NoticiaComponent } from '../pages/blog/noticia/noticia.component';
import { CatsaludComponent } from '../pages/blog/catsalud/catsalud.component';
import { CatgraciosoComponent } from '../pages/blog/catgracioso/catgracioso.component';
import { CatcuriosiComponent } from '../pages/blog/catcuriosi/catcuriosi.component';
import { CatvideoComponent } from '../pages/blog/catvideo/catvideo.component';
import { CattecnoComponent } from '../pages/blog/cattecno/cattecno.component';
import { CatmanualComponent } from '../pages/blog/catmanual/catmanual.component';

import { EstadisticasComponent } from '../pages/estadisticas/estadisticas.component';
import { UsuariosComponent } from '../pages/usuarios/usuarios.component';
import { LoginComponent } from '../pages/login/login.component';
import { PerfilComponent } from '../pages/perfil/perfil.component';
import { UrlsComponent } from '../pages/urls/urls.component';
import { TemporalComponent } from '../pages/temporal/temporal.component';
import { ErrorComponent } from '../pages/error/error.component';
import { AppComponent } from '../app.component';
import { ShowurlComponent } from '../pages/showurl/showurl.component';

import { InfoComponent } from '../pages/info/info.component';
import { ContactComponent } from '../pages/contact/contact.component';
import { EstadadminComponent } from '../pages/estadadmin/estadadmin.component';

/*********************************
Configuración de las Rutas del APP
**********************************/

const appRoutes: Routes = [
	{ path: 'login', component: LoginComponent},
    { path: '', redirectTo: 'home', pathMatch: 'full' },

    { path: 'home', component: HomeComponent },
    { path: 'temporal', component: TemporalComponent },
    { path: 'noticia', component: NoticiaComponent },
    { path: 'info', component: InfoComponent },
    { path: 'contact', component: ContactComponent },

    { path: 'estadisticas', component: EstadisticasComponent, canActivate: [AuthGuards],data: {Role: ['Administrador','Moderador', 'Usuarios']} },
    { path: 'usuarios', component: UsuariosComponent, canActivate: [AuthGuards],data: {Role: ['Administrador']} },
    { path: 'perfil', component: PerfilComponent, canActivate: [AuthGuards],data: {Role: ['Administrador','Moderador', 'Usuarios']} },
    { path: 'urls', component: UrlsComponent, canActivate: [AuthGuards],data: {Role: ['Administrador','Moderador', 'Usuarios']} },
    { path: 'showurl/:id', component: ShowurlComponent, canActivate: [AuthGuards],data: {Role: ['Administrador','Moderador', 'Usuarios']} },
    { path: 'addnotice', component: AddnoticeComponent, canActivate: [AuthGuards],data: {Role: ['Administrador','Moderador']} },
    { path: 'estadadmin', component: EstadadminComponent, canActivate: [AuthGuards],data: {Role: ['Administrador']} },

    { path: 'categoria/salud/:id', component: TemporalComponent},
    { path: 'categoria/entretenimiento/:id', component: TemporalComponent, canActivate: [ReferGuard]},
    { path: 'categoria/curiosidades/:id', component: TemporalComponent, canActivate: [ReferGuard]},
    { path: 'categoria/videos/:id', component: TemporalComponent, canActivate: [ReferGuard]},
    { path: 'categoria/tecnologia/:id', component: TemporalComponent, canActivate: [ReferGuard]},
    { path: 'categoria/manualidades/:id', component: TemporalComponent, canActivate: [ReferGuard]},

    { path: 'salud/:id', component: NoticiaComponent },
    { path: 'entretenimiento/:id', component: NoticiaComponent },
    { path: 'curiosidades/:id', component: NoticiaComponent },
    { path: 'videos/:id', component: NoticiaComponent },
    { path: 'tecnologia/:id', component: NoticiaComponent },
    { path: 'manualidades/:id', component: NoticiaComponent },

    { path: 'salud', component: CatsaludComponent },
    { path: 'entretenimiento', component: CatgraciosoComponent },
    { path: 'curiosidades', component: CatcuriosiComponent },
    { path: 'videos', component: CatvideoComponent },
    { path: 'tecnologia', component: CattecnoComponent },
    { path: 'manualidades', component: CatmanualComponent },

    { path: '404', component: ErrorComponent },
    { path: '**', redirectTo: '404' },
];

export const routing = RouterModule.forRoot(appRoutes);