import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';

import {Globals} from '../globals';
import { Base64 } from 'js-base64';

@Injectable({ providedIn: 'root' })
export class UrlsService {
  	constructor(private http: HttpClient, private globals: Globals) { }

  	getUrl(id, refer): Observable<any> {
        let us: any = JSON.parse(localStorage.getItem('currentUser'));
        return this.http.get<any>(`${this.globals.apiUrl}/url/` + id + '?user='+us+'&r='+Base64.encode(refer));
    }

    getCheckUrl(id, refer): Observable<any> {
        let us: any = JSON.parse(localStorage.getItem('currentUser'));
        return this.http.get<any>(`${this.globals.apiUrl}/urls/checkurl/` + id + '?user='+us+'&r='+Base64.encode(refer));
    }

    getUrlbyId(id, role): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/url/byid/` + id + '?role='+role);
    }

  	acortarUrl(url) {
        return this.http.post(`${this.globals.apiUrl}/urls/acortar`, url);
    }

    delUri(id) {
        return this.http.delete(`${this.globals.apiUrl}/urls/delete/` + id);
    }

    getEstadisticas(id): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/urls/estadisticas?user_id=`+id);
    }

    getAnalytic(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/urls/analytic`);
    }

    getMonthData(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/urls/monthdata`);
    }

    getEstadAdmin(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/urls/estadadmin`);
    }

    delUriAdmin(id) {
        return this.http.delete(`${this.globals.apiUrl}/urls/del/admin/` + id);
    }

    getUrlPop(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/urls/pop`);
    }

    setVisita(id): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/urls/visitas/` + id);
    }

    SetCpm(cpm) {
        return this.http.post(`${this.globals.apiUrl}/urls/cpm`, {cpm: cpm});
    }
}
