import { Component, OnInit } from '@angular/core';
import * as moment from '../../../../assets/plantilla/vendors/bower_components/moment/moment.js';
import { BlogService } from '../../../services/blog.service';
import {Globals} from '../../../globals';

@Component({
	selector: 'app-footerblog',
	templateUrl: './footerblog.component.html',
	styleUrls: ['./footerblog.component.css']
})
export class FooterblogComponent implements OnInit {

	anno: any = '2020'
	lastnotis: any = [];

	constructor(private blogService: BlogService, private globals: Globals) { }

	ngOnInit() {
		this.anno = moment().format('YYYY');

		this.blogService.getLastNotis3()
            .subscribe(data => {
                let da: any = data;
                this.lastnotis = da;
            })
	}

	getFecha(fecha) {
        return moment(fecha).format('YYYY/MM/DD H:mm');
    }
}
