import { AfterViewInit, Component, OnInit } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import {Globals} from '../../globals';
import * as moment from '../../../assets/plantilla/vendors/bower_components/moment/moment.js';
import * as Chartist from 'chartist';

import { UrlsService } from '../../services/urls.service';

declare var $: any;

@Component({
	selector: 'app-estadadmin',
	templateUrl: './estadadmin.component.html',
	styleUrls: ['./estadadmin.component.css']
})
export class EstadadminComponent implements OnInit {

    gdiarias: any = 0;
    gmensual: any = 0;
    fvdiarias: any = '';
    rpm: any = 0;
    table: any = '';
    dtOptions: any = {};
    tableurl: any = '';
    dtOptionsurl: any = {};
    ganancias: any = '';
    urls: any = '';

    constructor(private http: HttpClient, private globals: Globals, private urlsService: UrlsService) { }

    startAnimationForLineChart(chart){
        let seq: any, delays: any, durations: any;
        seq = 0;
        delays = 80;
        durations = 500;

        chart.on('draw', function(data) {
            if(data.type === 'line' || data.type === 'area') {
                data.element.animate({
                    d: {
                        begin: 600,
                        dur: 700,
                        from: data.path.clone().scale(1, 0).translate(0, data.chartRect.height()).stringify(),
                        to: data.path.clone().stringify(),
                        easing: Chartist.Svg.Easing.easeOutQuint
                    }
                });
            } else if(data.type === 'point') {
                seq++;
                data.element.animate({
                    opacity: {
                        begin: seq * delays,
                        dur: durations,
                        from: 0,
                        to: 1,
                        easing: 'ease'
                    }
                });
            }
        });
      	seq = 0;
    };

    async ngOnInit() {
        $("body").removeClass("off-canvas-sidebar");
        await this.getDataGanancias();
        await this.getDataUrl();
        this.table = $('#data-table').DataTable(this.fillTable());
        this.tableurl = $('#data-table-url').DataTable(this.fillTableUrl());
    }

    ngAfterViewInit(): void {
        var that = this;
        this.getEstadisticas();
        $('#data-table-url').on( 'click', '.btn-delete', function () {
            that.deleteUrl($(this).attr('date'));
        });
        $('[data-toggle="tooltip"]').tooltip('hide');
        $('[data-toggle=tooltip]').tooltip('hide');
        $("*").each(function () {
            var popover = $.data(this, "bs.tooltip");
            if (popover)
                $(this).tooltip('hide');
        });
    }

    getEstadisticas() {
        this.urlsService.getEstadAdmin()
            .subscribe(data => {
                let va: any = data;
                const decodedData = window.atob(va);
                let da: any = JSON.parse(decodedData);
                this.gmensual = da.gmensual.toFixed(2);
                this.gdiarias = da.gdiarias.toFixed(2);
                this.fvdiarias = da.fvdiarias;
                this.rpm = da.rpm;

                const dataGananciasChart: any = {
                    labels: da.chartganancias.dia,
                    series: [
                        da.chartganancias.ganancias
                    ]
                };

                const optionsGananciasChart: any = {
                    lineSmooth: Chartist.Interpolation.cardinal({
                        tension: 0
                    }),
                    low: 0,
                    high: da.maxg,
                    chartPadding: { top: 0, right: 0, bottom: 0, left: 0}
                }

                var gananciasChart = new Chartist.Line('#gananciasChart', dataGananciasChart, optionsGananciasChart)
                    .on("draw", function(data) {
                        if (data.type === "point") {
                            data.element._node.setAttribute("title", "Value: " + data.value.y);
                            data.element._node.setAttribute("data-chart-tooltip", "gananciasChart");
                        }
                    }).on("created", function() {
                        $("#gananciasChart").tooltip({
                            selector: '[data-chart-tooltip="gananciasChart"]',
                            container: "#gananciasChart",
                            html: true
                        });
                    }
                );

                this.startAnimationForLineChart(gananciasChart);
            }
        )
    }

    getDataGanancias() {
        var that = this;
        let promise = new Promise((resolve, reject) => {
            this.http.get(`${this.globals.apiUrl}/users/ganancias`)
                .toPromise()
                .then(
                    res => { // Success
                        that.ganancias = res;
                        resolve();
                    }
                );
        });
        return promise;
        /*$.ajax({
            url: that.globals.apiUrl+'/users/ganancias',
            "headers": {
                "Authorization": "bGl1c3Zhbmk6bHNiYXJ6YWdh"
            },
            success : function(data) {
                that.ganancias = data;
            }       
        });*/
    }

    getDataUrl() {
        var that = this;
        let promise = new Promise((resolve, reject) => {
            this.http.get(`${this.globals.apiUrl}/urls/admin`)
                .toPromise()
                .then(
                    res => { // Success
                        that.urls = res;
                        resolve();
                    }
                );
        });
        return promise;
        /*$.ajax({
            url: this.globals.apiUrl+'/urls/admin',
            "headers": {
                "Authorization": "bGl1c3Zhbmk6bHNiYXJ6YWdh"
            },
            success : function(data) {
                that.urls = data;
            }       
        });*/
    }

    fillTable() {
        var that = this;
        return this.dtOptions = {
            pageLength: 10,
            autoWidth: !1,
            responsive: !0,
            "destroy": true,
            "order": [[5, 'desc']],
            language: {
                "url": "assets/Spanish.json",
                searchPlaceholder: "Escriba parametro a filtrar..."
            },
            ajax: function (data, callback, settings) {
                callback(
                    JSON.parse(window.atob(that.ganancias))
                );
            },
            columns: [
                { title: 'Nombre y Apellidos', data: 'nombre_apellidos', className: "align-middle", "render": function ( data, type, row, meta ) {
                    return data;
                }},
                { title: 'Correo', data: 'correo', className: "align-middle", "render": function ( data, type, row, meta ) {
                    return  '<i class="zmdi zmdi-account-box-mail"></i> '+data;
                } },
                { title: 'Perfil FB', data: 'perfil_fb', className: "align-middle", "render": function ( data, type, row, meta ) {
                    return  '<i class="zmdi zmdi-facebook"></i> '+data;
                }},
                { title: 'Rol', data: 'id', className: "align-middle", "render": function ( data, type, row, meta ) {
                    return  '<mat-chip-list _ngcontent-c4="" class="mat-chip-list" tabindex="0" aria-required="false" aria-disabled="false" aria-invalid="false" aria-multiselectable="false" role="listbox" aria-orientation="horizontal" id="mat-chip-list-1"><div class="mat-chip-list-wrapper"><mat-chip _ngcontent-c4="" class="mat-chip mat-standard-chip mat-accent mat-chip-selected" color="accent" role="option" selected="" ng-reflect-color="accent" ng-reflect-selected="" tabindex="-1" aria-disabled="false" aria-selected="true">'+row.roles[0].name+'</mat-chip></div></mat-chip-list>';
                }},
                { title: 'Activo', data: 'activo', className: "align-middle", "render": function ( data, type, row, meta ) {
                    return  (data == 1 ? '<i class="zmdi zmdi-check-circle-u zmdi-hc-2x"></i>' : '<i class="zmdi zmdi-dot-circle zmdi-hc-2x"></i>');
                }},
                { title: 'Ganancia Brutas', data: 'gan', "render": function ( data, type, row, meta ) {
                    return  (data != null ? '<i class="zmdi zmdi-money"></i> '+ data.toFixed(2) : 0);
                }},
                { title: 'A Pagar', data: 'id', className: "align-middle", "render": function ( data, type, row, meta ) {
                    if(row.gan != null)
                        return (row.roles[0].name == "Administrador" ? '<i class="zmdi zmdi-money"></i> '+ row.gan.toFixed(2) : (row.roles[0].name == "Moderador" ? '<i class="zmdi zmdi-money"></i> '+ (row.gan * 60 / 100).toFixed(2) : '<i class="zmdi zmdi-money"></i> '+ (row.gan * 50 / 100).toFixed(2)));
                    else
                        return '0';
                }},
            ],
            dom: '<"dataTables__top"lfB>rt<"dataTables__bottom"ip><"clear">',
            buttons: [{
                extend: "excelHtml5",
                title: "Export Data"
            }, {
                extend: "csvHtml5",
                title: "Export Data"
            }, {
                extend: "print",
                title: "Material Admin"
            }],
            "initComplete": function () {
                $('[data-toggle="tooltip"]').tooltip();
                $(this).closest(".dataTables_wrapper").find(".dataTables__top").prepend('<div class="dataTables_buttons hidden-sm-down actions"><span class="actions__item zmdi zmdi-print" data-table-action="print" /><span class="actions__item zmdi zmdi-fullscreen" data-table-action="fullscreen" /><div class="dropdown actions__item"><i data-toggle="dropdown" class="zmdi zmdi-download" /><ul class="dropdown-menu dropdown-menu-right"><a href="" class="dropdown-item" data-table-action="excel">Excel (.xlsx)</a><a href="" class="dropdown-item" data-table-action="csv">CSV (.csv)</a></ul></div></div>')
            },
        };
    }

    fillTableUrl() {
        var that = this;
        return this.dtOptionsurl = {
            pageLength: 10,
            autoWidth: true,
            fixedColumns: true,
            responsive: true,
            "destroy": true,
            "order": [[0, 'desc']],
            language: {
                "url": "assets/Spanish.json",
                searchPlaceholder: "Escriba parametro a filtrar..."
            },
            ajax: function (data, callback, settings) {
                callback(
                    JSON.parse(window.atob(that.urls))
                );
            },
            columns: [
                { title: 'Fecha', data: 'fecha', className: "align-middle", "render": function ( data, type, row, meta ) {
                      return moment(data).format('YYYY/MM/DD H:mm');
                }},
                { title: 'Acortada', data: 'url_acortada', className: "align-middle", "render": function ( data, type, row, meta ) {
                    return '/categoria/'+row.categoria.categoria+'/'+data;
                } },
                { title: 'Real', data: 'url_real', className: "align-middle", "render": function ( data, type, row, meta ) {
                    return data;
                }},
                { title: 'Usuario', data: 'id', className: "align-middle", "render": function ( data, type, row, meta ) {
                    return row.users.nombre_apellidos;
                    }},
                { title: 'Visitas', data: 'visitas', className: "align-middle", "render": function ( data, type, row, meta ) {
                    return data;
                }},
                { title: 'Ganancia Brutas', data: 'gan', "render": function ( data, type, row, meta ) {
                    return  (data != null ? '<i class="zmdi zmdi-money"></i> '+ data.toFixed(2) : 0);
                }},
                { title: 'Opciones', data: 'id', "render": function ( data, type, row, meta ) {
                    let eliminar = '<button date="'+data+'" title="Eliminar url" data-toggle="tooltip" class="mat-icon-button mat-accent btn-delete"><span class="mat-button-wrapper"><mat-icon class="mat-icon material-icons" role="img" aria-hidden="true">delete_sweep</mat-icon></span><div class="mat-button-ripple mat-ripple mat-button-ripple-round" matripple="" ng-reflect-centered="true" ng-reflect-disabled="false" ng-reflect-trigger="[object HTMLButtonElement]"></div><div class="mat-button-focus-overlay"></div></button>';
                    return eliminar;
                }}
            ],
            "columnDefs": [
                { "width": "150px", "targets": 0 },
                { "width": "250px", "targets": 1 },
                { "width": "500px", "targets": 2 },
                { "width": "300px", "targets": 3 },
                { "width": "60px", "targets": 4 },
                { "width": "100px", "targets": 5 },
                { "width": "70px", "targets": 6 }
            ],
            dom: '<"dataTables__top"lfB>rt<"dataTables__bottom"ip><"clear">',
            buttons: [{
                extend: "excelHtml5",
                title: "Export Data"
            }, {
                extend: "csvHtml5",
                title: "Export Data"
            }, {
                extend: "print",
                title: "Material Admin"
            }],
            "initComplete": function () {
                $('[data-toggle="tooltip"]').tooltip();
                $(this).closest(".dataTables_wrapper").find(".dataTables__top").prepend('<div class="dataTables_buttons hidden-sm-down actions"><span class="actions__item zmdi zmdi-print" data-table-action="print" /><span class="actions__item zmdi zmdi-fullscreen" data-table-action="fullscreen" /><div class="dropdown actions__item"><i data-toggle="dropdown" class="zmdi zmdi-download" /><ul class="dropdown-menu dropdown-menu-right"><a href="" class="dropdown-item" data-table-action="excel">Excel (.xlsx)</a><a href="" class="dropdown-item" data-table-action="csv">CSV (.csv)</a></ul></div></div>')
            },
        };
    }

    deleteUrl(id) {
        if(confirm("Esta Seguro que desea eliminar la URL?")) {
            this.urlsService.delUriAdmin(id)
                .subscribe(data => {
                    this.tableurl = $('#data-table-url').DataTable(this.fillTableUrl());
                    this.showMessage("Url eliminada");
                    $('[data-toggle="tooltip"]').tooltip('hide');
                    $('[data-toggle=tooltip]').tooltip('hide');
                    $("*").each(function () {
                        var popover = $.data(this, "bs.tooltip");
                        if (popover)
                            $(this).tooltip('hide');
                    });
                }
            );
        }
    }

    Ganancias() {
        this.urlsService.getAnalytic()
            .subscribe(data => {
                let da: any = data;
                this.showMessage("Ganancias actualizadas");
                location.reload(true);
            }
        );
    }

    CPM() {
        if($('#cpm').val() == '') {
            this.showMessage("Por favor, escriba el cpm");
            return false;
        }
        else {console.log($('#cpm').val());
            this.urlsService.SetCpm($('#cpm').val())
                .subscribe(data => {
                    let da: any = data;
                    console.log(da);
                }
            );
        }
    }

    showMessage(message: string) {
        $.notify({
            icon: 'notifications',
            title: ' Notificación',
            message: message,
            url: ''
        },{
            type: 'info',
            allow_dismiss: true,
            placement: {
                from: 'top',
                align: 'center'
            },
            timer: 1000,
            animate: {
                enter: 'fadeInDown',
                exit: 'fadeOutUp'
            },
            template: '<div data-notify="container" class="col-xl-4 col-lg-4 col-11 col-sm-4 col-md-4 alert alert-{0} alert-with-icon" role="alert">' +
                      '<button mat-button  type="button" aria-hidden="true" class="close mat-button" data-notify="dismiss">  <i class="material-icons">close</i></button>' +
                      '<i class="material-icons" data-notify="icon">notifications</i> ' +
                      '<span data-notify="title">{1}</span> ' +
                      '<span data-notify="message">{2}</span>' +
                      '<div class="progress" data-notify="progressbar">' +
                        '<div class="progress-bar progress-bar-{0}" role="progressbar" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100" style="width: 0%;"></div>' +
                      '</div>' +
                      '<a href="{3}" target="{4}" data-notify="url"></a>' +
                      '</div>'
        });
    }

}
