import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';

import {Globals} from '../globals';

@Injectable({
	providedIn: 'root'
})
export class BlogService {

	constructor(private http: HttpClient, private globals: Globals) { }

	removePhotob(id: number, photob) {
        return this.http.get(`${this.globals.apiUrl}/blog/removephotob?photob=`+photob+`&id=`+id);
    }

    crearNoti(noti) {
        return this.http.post(`${this.globals.apiUrl}/blogs/create`, noti);
    }

    updateNoti(id, noti) {
        return this.http.put(`${this.globals.apiUrl}/blogs/update/` + id, noti);
    }

    delNoti(id) {
        return this.http.delete(`${this.globals.apiUrl}/blogs/delete/` + id);
    }

    getNoti(id): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blog/` + id);
    }

    getNotiEdit(id): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blog/edit/` + id);
    }

    getLastNotis3(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/lastnoti3`);
    }

    getPopular(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/popular`);
    }

    getPopularSalud(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/lastsalud`);
    }

    getPopularCurio(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/lastcuriosidad`);
    }

    getPopularManual(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/lastmanual`);
    }

    getPopularEntret(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/lastentre`);
    }

    getPopularVideo(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/lastvideo`);
    }

    getPopularTecno(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/lasttecno`);
    }

    getAllSalud(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/allsalud`);
    }

    getAllGracioso(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/allgracioso`);
    }

    getAllCurio(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/allcuriosidades`);
    }

    getAllVideo(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/allvideo`);
    }

    getAllTecno(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/alltecnologia`);
    }

    getAllManual(): Observable<any> {
        return this.http.get<any>(`${this.globals.apiUrl}/blogs/allmanualidades`);
    }

    crearMensaje(mensaje) {
        return this.http.post(`${this.globals.apiUrl}/blogs/create/mensaje`, mensaje);
    }
}
